<script>
import login from './components/login'
import home from './components/home'
export default {
  components: {
    home,
    login
  },
  data () {
    return {
      is_login: false
    }
  },
  methods: {
    show () {
      this.is_login = !this.is_login
    }
  }
}
</script>
